// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'

import App from './App'

// Router
import VueRouter from 'vue-router'
Vue.use(VueRouter)

// Element UI
import ElementUI from 'element-ui'
Vue.use(ElementUI)
import 'element-ui/lib/theme-chalk/index.css'

// Vant
import Vant from 'vant'
import 'vant/lib/index.css'
import 'vant/lib/icon/local.css'
import '@vant/touch-emulator'
// Vue.use(Vant) 


// Axios
import axios from 'axios'
Vue.prototype.$axios = axios

// Video.js
import Video from 'video.js'
import 'video.js/dist/video-js.css'

// iconfont
import '../static/icon/line-icon/iconfont.css'
import '../static/icon/music-controls/iconfont.css'
Vue.prototype.$video = Video
// jsonp
import { VueJsonp } from 'vue-jsonp'
Vue.use(VueJsonp)
// import JsonpCommon from './js/jsonp-common.js'
// Vue.use(JsonpCommon)


Vue.config.productionTip = false

import Course from '@/pages/Course'
import HelloPage from '@/pages/HelloPage'
import Fanya from '@/pages/Fanya'

// 1.  () 
//  import 
const Test = {
  template: '<div style="display:flex;color:#fff;font-size:40px;height:100%;width:100vw;align-items:center;justify-content:center;background:#ffffff56"><span></span></div>'
}

// 2. 
//  "component" 
//  Vue.extend() 
// 
// 
const routes = [{
    path: '/course',
    component: Course
  },
  {
    path: '/test',
    component: Test
  },
  {
    path: '/',
    component: HelloPage
  }, {
    path: '/fanya',
    component:Fanya
  }
]

// 3.  router  `routes` 
// , 
const router = new VueRouter({
  routes // ()  routes: routes
})

// 4. 
//  router 
// 

// 
/* eslint-disable no-new */
new Vue({
  router,
  el: '#app',
  render: h => h(App),
  components: {
    App
  },
  template: '<App/>'
})



